@import url(https://fonts.googleapis.com/css?family=Spartan|Ubuntu&display=swap);
body {
  margin: 0;
  font-family: 'Ubuntu','Segoe UI', 'Roboto', 'Oxygen', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#fafafa;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

code {
  font-family: 'Ubuntu',source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-nav{width:100%;position:fixed;padding:30px 0;background-color:white;font-family:'Spartan', sans-serif;font-size:1.3em;z-index:10000000;display:flex;align-items:flex-end;transition:all 1s ease;border-bottom:1px solid lightgray}.main-nav .main-links{text-align:right;width:100%}.main-nav a{color:black;position:relative;right:20px;margin:0px 25px;transition:all 1s ease;text-decoration:none}.main-nav :hover{text-decoration:none;cursor:pointer}.main-nav-l{width:100%;text-align:right;position:fixed;padding:30px 0;background-color:transparent;font-family:'Spartan', sans-serif;font-size:1.3em;z-index:10000000;display:inline-block;right:0;transition:all 1s ease}.main-nav-l a{text-align:right;color:white;position:relative;right:20px;margin:0px 25px;transition:all 1s ease}.main-nav-l :hover{text-decoration:none;cursor:pointer}div#internal{background-image:linear-gradient(#7400D4, #001655)}div#internal a{color:white !important}.main-nav-color{width:100%;padding:15px 0;text-align:right;position:fixed;background-color:rgba(56,2,147,0.7);font-family:'Spartan', sans-serif;font-size:1.3em;z-index:10000000;display:inline-block;right:0;transition:all 1s ease}.main-nav-color a{text-align:right;color:white;position:relative;right:20px;margin:0px 25px;transition:all 1s ease}.main-nav-color :hover{text-decoration:none;cursor:pointer}.logo{width:10em;cursor:pointer;z-index:10000}.logo img{width:15rem;padding-left:2rem}.logo-scroll{width:6em !important;position:relative;cursor:pointer;z-index:10000;display:block;display:inline-block;float:left}img.logo-l{float:left}img.logo-l2{float:left;width:6em !important}.logo-internal{width:8%;position:relative;left:30px;z-index:10000;display:inline-block;float:left;font-family:'fira sans'}a.logo:after{content:"";position:absolute;top:0;right:0;bottom:0;left:0}a.logo-scroll:after{content:"";position:absolute;top:0;right:0;bottom:0;left:0}@media handheld, screen and (max-width: 900px){a#logo{position:absolute;left:1.5em;top:2em}.sidenav{height:100%;width:0;position:fixed;z-index:1;top:0;right:0;background-color:white;overflow-x:hidden;padding-top:9em;transition:0.3s;z-index:1000000}.sidenav a{padding:15px 1em 10px 32px;text-decoration:none;text-align:right;font-size:25px;color:#00002b;display:block;transition:0.3s;font-size:1.6em;white-space:nowrap}.sidenav a:hover{color:#f1f1f1}.sidenav .closebtn{position:absolute;top:0;right:25px;font-size:36px;margin-left:50px}.navbar{display:none}.header{display:none}.mobile-nav{cursor:pointer;position:fixed;right:1em;top:3em;z-index:10000000000;width:auto !important;width:initial !important}.bar1,.bar2,.bar3{width:34px;height:4px;background-color:black;margin:7px 0;border-radius:12px;transition:0.4s}.change .bar1{transform:rotate(-45deg) translate(-9px, 7px)}.change .bar2{opacity:0}.change .bar3{transform:rotate(45deg) translate(-8px, -7px)}img#logo-menu{width:1.2em}}

img#mobile-logo{width:10rem;position:absolute;top:3.5rem;left:1.5rem}.marketing{padding-top:130px;background:linear-gradient(45deg, #5f5fd3, transparent);display:grid;align-items:center;grid-template-columns:50% 50%;grid-column-gap:5rem}.marketing img{width:70%;justify-self:flex-end}.marketing .statement{justify-self:flex-start;width:60%}.marketing .statement p{font-size:1.3rem;line-height:35px;color:rgba(0,0,43,0.8)}.marketing .statement h1{font-weight:400;color:rgba(0,0,43,0.8)}.marketing .statement button{padding:1.3rem 3rem;border-radius:5px;border:none;background-color:white;font-size:1.1rem;color:darkslateblue;font-family:'Spartan';font-weight:600;cursor:pointer}h1#why-heading{text-align:center;font-family:'Spartan', sans-serif;font-weight:400;margin:5rem 0}.why{display:grid;grid-template-columns:35% 35%;justify-content:center;align-items:center;min-height:100vh;grid-column-gap:3rem;margin-bottom:10rem;grid-row-gap:3rem}.why .feat-item{display:grid;align-items:center;justify-content:center;grid-template-columns:70% auto;grid-column-gap:0rem;box-shadow:0px 0px 30px lightgray;padding:5rem;border-radius:5px}.why .feat-item a{width:100%}.why .feat-item img{width:15rem}.why .feat-item button{padding:1.3rem 3rem;border-radius:5px;border:none;background-color:darkslateblue;font-size:1.1rem;color:white;font-family:'Spartan';font-weight:600;cursor:pointer;width:75%}.why h1{color:rgba(0,0,0,0.8)}.contact-us{border-top:1rem solid darkslateblue;padding:5rem 0;width:90%;margin:0 auto;border-radius:10px}.contact-us h1{width:90%;text-align:center;font-family:sans-serif;color:rgba(0,0,0,0.7);margin:0 auto;padding-top:3rem}@media handheld, screen and (max-width: 900px){.marketing{grid-template-columns:auto;grid-row-gap:5rem;background:linear-gradient(0deg, #5f5fd3, transparent 50%)}.marketing .statement{width:90%;justify-self:center}.marketing img{grid-row-start:2;justify-self:center;padding-bottom:4rem}h1#why-heading{margin:3rem 0 0}.why{display:flex;flex-direction:column}.why .feat-item{display:flex;flex-direction:column;width:80%;text-align:center;margin:2rem 0;padding:1rem}.why .feat-item img{width:10rem;margin:1rem 0 2rem}.contact-us{padding-top:2rem}.contact-us h1{padding-top:0}}

.form-component{background-color:#fafafa;padding:1rem 0}.form-component form{display:flex;flex-direction:column;width:30%;margin:0 auto;border-radius:5px;padding:4rem}.form-component form input,.form-component form select{height:3rem;margin:1rem 0px;border-radius:5px;border:1px solid gray;padding:0 1rem;font-family:'Spartan', sans-serif;font-size:1.3rem;background-color:white}.form-component form input[type="submit"]{background-color:darkslateblue;color:white;-webkit-appearance:none;-moz-appearance:none;appearance:none}.form-component form span{color:darkred}@media handheld, screen and (max-width: 900px){.form-component form{width:85%;padding:1rem}}

.main-info{min-height:50vh;display:grid;grid-template-columns:30% 30% 30%;justify-content:center;align-items:center;justify-items:center}.main-info .services-imgs{padding:2rem;box-shadow:0 0 30px lightgray;margin:3rem 0;width:80%;border-radius:10px;text-align:center;min-height:70%}.main-info .services-imgs img{width:10rem}.main-info .services-imgs p{text-align:center;font-family:'Ubuntu', sans-serif;font-size:1.2rem;color:rgba(0,0,0,0.8)}.main-info .services-imgs h1{font-weight:500;color:darkslateblue}@media handheld, screen and (max-width: 900px){.main-info{grid-template-columns:auto}.main-info .services-imgs{padding:1rem;margin:1rem 0;width:80%;border-radius:10px;text-align:center;min-height:20rem}}

.footer{height:15rem;background:rgba(95,95,211,0.2);color:white}.footer .footer-content{height:100%;display:grid;grid-template-columns:auto auto auto;grid-template-rows:auto;width:50%;margin:0 auto}.footer .footer-content .footer-col{margin:0 auto;display:flex;flex-direction:column;justify-content:baseline;height:100%}.footer .footer-content .footer-col a{color:rgba(0,0,0,0.7);font-family:'Spartan', sans-serif;text-decoration:none;font-weight:600;padding:.2rem}.footer .footer-content .footer-col h3{color:rgba(0,0,0,0.8)}.legal{color:rgba(0,0,0,0.8);padding-top:10rem;width:80%;margin:0 auto;line-height:2.5rem}

